import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql, withPrefix } from 'gatsby'
import format from 'date-fns/format'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Image from 'components/Image';
import theme, { mobileOrDesktop } from 'components/ThemeProvider/theme';

import menudoc from './menudoc.svg'
import Header from '../Header'
import Button from '../../components/Button';
import Link from '../../components/Link';

const menus =[
  {
    label: '認識識能小助手',
    to: '/'
  },
  {
    label: '製作看診備忘錄',
    to: '/memo'
  },
  {
    label: '看診中注意事項',
    to: '/notice'
  },
  {
    label: '檢測你的看診表現',
    to: '/test/start'
  },
]

const Layout = ({ children, noHeader, noFooter, intl, ...props }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
            description
            url
          }
        }
      }
    `}
    render={({ site: { siteMetadata } }) => {
      const title = siteMetadata.title;
      const description = siteMetadata.description;
      const [currentState, setState] = useState(0)
      return (
        <div>
          <Helmet>
            <html lang="zh-Hant-TW" />
            <title>{title}</title>
            <meta name="description" content={description} />
            <link rel="apple-touch-icon" sizes="180x180" href={withPrefix('/apple-touch-icon.png')} />
            <link rel="icon" type="image/png" sizes="32x32" href={withPrefix('/favicon-32x32.png')} />
            <link rel="icon" type="image/png" sizes="16x16" href={withPrefix('/favicon-16x16.png')} />
            <link rel="mask-icon" color="#5bbad" href={withPrefix('/safari-pinned-tab.svg')} />
            <meta name="msapplication-TileColor" content="#da532c" />
            <meta name="theme-color" content="#ffffff" />
            <meta property="og:url" content={siteMetadata.url} />
            <meta property="og:title" content={title} />
            <meta property="og:description" content={description} />
            <meta property="og:image" content={`${siteMetadata.url}/fb.png`} />
          </Helmet>
          {!noHeader && (
            <Header
              height={theme.headerHeight}
              siteTitle={title}
              menuClick={() => Boolean(currentState) ? setState(0) : setState(1)}
              currentState={currentState}
            />)}
          <Box minHeight="100vh" pt={!noHeader && theme.headerHeight} pb={!noFooter && theme.footerHeight} {...props}>
            {children}
          </Box>
          <Flex
            mt={`-${theme.footerHeight}`}
            height={theme.footerHeight}
            bg="titleBlue"
            alignItems="center"
            justifyContent="center"
          >
            <Text color="white" fontSize="0.8em">
              Copyright © {format(new Date(), 'YYYY')}. All rights reserved.
            </Text>
          </Flex>
          {Boolean(currentState) && (
            <Box position="fixed"
              top="0"
              bottom="0"
              left="0"
              right="0"
              bg="rgba(0,0,0,0.7)"
              onClick={() => setState(0)}
              cursor="unset"
            />
          )}
          <Box
              bg="bgOrange"
              position="fixed"
              top="0"
              bottom="0"
              transform={Boolean(currentState) ? 'translateX(0)' : 'translateX(-100%)'}
              transition="transform 0.5s"
              pt={theme.headerHeight}
            >
              {menus.map(({ label, to }) => (
                <Box pt="1em" key={label}>
                  <Button.menu width={1} is={Link} to={to}>
                    {label}
                  </Button.menu>
                </Box>
              ))}
              <Box position="absolute" left="0" bottom="0" width={mobileOrDesktop('60%', '80%')}>
                <Image src={menudoc} />
              </Box>
            </Box>
        </div>
      );
    }}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
